import { Component, OnInit, ɵConsole } from '@angular/core';
import { Route } from '@angular/compiler/src/core';
import { ActivatedRouteSnapshot, ActivatedRoute } from '@angular/router';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { ModalController, NavController } from '@ionic/angular';
import { ListRedirectPage } from '../../util/list-redirect/list-redirect.page';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { FormPage } from '../form/form.page';
import { UtilArrayService } from 'src/app/services/util-array.service';
import { AlertCreateTeamPage } from '../../payment/alert-create-team/alert-create-team.page';
import { PaymentTeamService } from 'src/app/services/payment-team.service';
import { SharerService } from 'src/app/services/sharer.service';
import { ListFieldsPage } from '../../field/list-fields/list-fields.page';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ScreenLoadingService } from 'src/app/services/screen-loading.service';
import { from, Observable } from 'rxjs';
import { retry, catchError, take, map } from 'rxjs/operators';
import { HtmlToImageService } from 'src/app/services/html-to-image.service';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {

  public id : string = '0';
  public teamModel : Model;
  public team :any = null;
  public isAdmin = false;
  public canEdit = false;

  playersList : any = [];
  playersLoading = true;
  playersErrorLoad = false;

  response;


  constructor(
    public route : ActivatedRoute,
    public requesService : RequestService,
    public modalController : ModalController,
    public navCtrl : NavController,
    public authUser : AuthUserService,
    public utilArray : UtilArrayService,
    public paymentTeam : PaymentTeamService,
    public shareService : SharerService,
    public dialogService : DialogService,
    public loading : ScreenLoadingService,
    public htmlToImageService : HtmlToImageService,

  ) {

    this.id = this.route.snapshot.paramMap.get('id');
    this.teamModel = new Model('Team',requesService);

    this.authUser.userOb.subscribe(()=>{
      this.init();
    })

   }

  async ngOnInit() {
    this.init()
  }

  init($events = null){

    if(! $events)
      this.loading.start();


    this.playersLoading = true;
    
    this.teamModel.api_functionModel(this.id,'pageProfile').subscribe(
      response => {

        if(response['status'] == 'success'){
          this.team = response['data'];
          this.paymentTeam.setTeam(this.team);
          this.playersList = this.team.players;
        }
        this.playersLoading = false;
        this.loadPermissions();
        if($events)
          $events.target.complete();

        this.loading.end();
      },
      error => {
        this.playersLoading = false;
        this.playersErrorLoad = false;

        
        if($events)
          $events.target.complete();

        this.loading.end();
      }
    )

  }


  loadPermissions(){
    this.isAdmin = this.authUser.isAdminOfTeam(this.id);
  }

  getCover(){
    return this.team.cover_page ? this.team.cover_page.urlComplete : 'assets/images/team3.png' ; 
  }

  getShield(){
    return this.team.shield ? this.team.shield.urlComplete : 'assets/images/esc.png';
  }

  async showMenu(){
    let list = [
      {
        name : 'Editar Equipo',
        route : ''
      },
      {
        name : 'Admi. Jugadores',
        route : `team/${this.id}/players`
      },
      {
        name : 'Admi. Partidos',
        rote : ''
      }
    ];
    const modal = await this.modalController.create({
      component: ListRedirectPage ,
      componentProps: { 
        title : 'Administrar',
        list: list }
    });

    modal.onDidDismiss().then(data=>{
      console.log(data);
      
    })

    return await modal.present();
   }

   setLog(num){
     console.log('set log in page profile, num : ' , num)
   }

   async adminPlayers(){

    if(this.paymentTeam.noPaid()){
      await this.paymentTeam.showAlert();
      return;
    }

     this.navCtrl.navigateForward(`/team/${this.id}/players`);
   }

   loadPlayers(event){
     this.playersLoading = true;
      this.teamModel.api_loadAttribute(this.id,'players').subscribe(
        response => {

          if(response['status'] == 'success'){
            this.playersList = response['Team']['players'];
          }
          this.playersLoading = false
        },
        error => {
          this.playersLoading = false
          this.playersErrorLoad = true;
        }
      )
   }

   playerSelect(player){
      console.log('goTo',player)
      this.navCtrl.navigateForward(`player/profile/${player.id}`);
    
   }


   async showEdit(){

    if(this.paymentTeam.noPaid()){
      await this.paymentTeam.showAlert();
      return;
    }


    const modal = await this.modalController.create({
      component: FormPage ,
      componentProps: { 
        id: this.id,
        adminTeam : true
      }
    });

    modal.onDidDismiss().then(data=>{
      console.log('return edit')
      if(data.data && data.data.team){
        const team = data.data['team'];
        this.utilArray.updateAttribustesObject(this.team , team , ['name','shield_id','cover_page_id','shield','cover_page']);
      }
    })

    return await modal.present();
   }


  ionViewDidLeave(){
    this.shareService.hideAddThis();
  }

  async selectField(){

    if(this.paymentTeam.noPaid()){
      await this.paymentTeam.showAlert();
      return;
    }

    const modal = await this.modalController.create({
      component : ListFieldsPage
    })

    modal.onDidDismiss().then(data=>{
      console.log('close modal select field')
      if(data.data && data.data['field']){
        
        this.addField(data.data['field'])

      }
    })

    await  modal.present();
  }

  async addField(field){
    await this.dialogService.loadingProgres('Añadiendo Cancha')

    this.teamModel.api_functionModel(this.id,'addField',{field_id:field.id}).subscribe(
      response => {
          if(response['status'] == 'success'){
            if(response['data'] && response['data']){
              this.team.field = response['data'] ;
            }
          }
          this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging();
      }
    )
  }

  confirmDeleteField(field){
    //$event.stopPropagation();

    this.dialogService.presentAlertConfirm('Confirmar','Estas seguro de eliminar esta cancha',e => {
      this.deleteField(field)
    });
   }

   async deleteField(field){
    await this.dialogService.loadingProgres('eliminando...') 
    this.teamModel.api_functionModel(this.id,'removeField',{field_id : field.id }).subscribe(data => {
       console.log(data);
       if(data['status'] == 'success'){
         this.dialogService.showToast('Cancha Eliminada',null,'success');
         this.team.field = null;
       }
       this.dialogService.dimissLoaging();
     });
   }

}
